import React, { Component, Fragment } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import "react-table/react-table.css";

import SettingModal from "./SettingModal";
import { get, deleteApi, post } from "../../api";
import Button from "../../components/Common/Button";
import Table from "../../components/Common/Table";
import Alert from "../../components/Alert";
import { settingsContainer, settingsNewButtonContainer, settingsDesignList } from "./styles";

class Settings extends Component {
    state = {
        homeBackground: 2,
        showModal: false,
        designs: []
    };

    componentDidMount() {
        const apiOptions = JSON.stringify({
            offset: 0,
            limit: 100,
            status: [true, false]
        });

        get(`/posts/get/all/${apiOptions}`).then(data => {
            const designs = data.data;

            this.setState(
                {
                    designs
                },
                () => {
                    this.handleInitBackground();
                }
            );
        });
    }

    handleInitBackground = () => {
        get("/configurations/get/all").then(response => {
            const { designs } = this.state;
            const parsedImageUrl = designs.find(
                design => response.data[response.data.length - 1].value === design.images[0].url
            );

            this.setState({
                homeBackground: parsedImageUrl.id
            });
        });
    };

    handleAddDesignClick = () => {
        const { showModal } = this.state;
        this.setState({
            showModal: !showModal
        });
    };

    handleDeleteDesign = id => {
        deleteApi("/posts/delete/", { id })
            .then(response => {
                if (response.status.success) {
                    const { designs } = this.state;
                    this.setState({
                        designs: designs.filter(design => design.id !== id)
                    });
                    Alert("success", "Hapus design berhasil");
                } else {
                    Alert("error", response.message);
                }
            })
            .catch(error => {
                Alert("error", error);
            });
    };

    handleChangeBackground = id => {
        const { designs } = this.state;
        const submittedImgUrl = designs.filter(design => design.id === id);
        post("/configurations/create", {
            key: "home-background",
            value: submittedImgUrl[0].images[0].url,
            status: true
        })
            .then(response => {
                if (response.status.success) {
                    this.setState({
                        homeBackground: id
                    });

                    Alert("success", "Background berhasil diganti");
                } else {
                    Alert("error", response.message || "Background gagal diganti");
                }
            })
            .catch(error => {
                Alert("error", "Server Error: ", error);
            });
    };

    handleCloseModal = () => this.setState({ showModal: false });

    render() {
        const { designs, homeBackground, showModal } = this.state;
        return (
            <Fragment>
                <SettingModal isModalShow={showModal} onCloseModal={this.handleCloseModal} />
                <div className={settingsContainer}>
                    <div className={settingsNewButtonContainer}>
                        <Button
                            type="button"
                            classBtn="success"
                            onClick={this.handleAddDesignClick}
                        >
                            New
                            <FontAwesomeIcon icon="plus" size="sm" />
                        </Button>
                    </div>
                    <div className={settingsDesignList}>
                        <Table
                            data={designs || []}
                            onChangeBackground={this.handleChangeBackground}
                            onDeleteDesign={this.handleDeleteDesign}
                            idBackground={homeBackground}
                            type="settings"
                        />
                    </div>
                </div>
            </Fragment>
        );
    }
}

export default Settings;
